@import "mixins";
@import "variables";
@import "progressiveImg";

html {
  font-size: 62.5%;
}

body {
  background-color: #FAFAFA !important;
  font-family: sans-serif, microsof yahei;
  a {
    text-decoration: none !important;
    &:hover {
      cursor: pointer;
      color: rgba(0, 0, 0, 0.8);
    }
  }
  p {
    margin: 0 !important;
  }
}

.navbar {
  background-color: rgba(0,0,0,.4);
  .container {
    line-height: 5rem;
  }
  .navbar-register-btn,
  .navbar-login-btn,
  .navbar-logo a,
  .navbar-btn-group {
    color: #FFF;
  }
}

.modal-header {
  border-bottom: none;
}

.home-banner {
  @include banner;
  // top: 5rem;
  .banner-content {
    top: 8rem;
  }
}

.article-preview-list {
  margin-top: 1rem;
}

.preview-article__readMore {
  margin-top: 1rem;
  a {
    font-size: 1.2rem;
    color: rgba(0, 0, 0, .44);
    transition: background-color .15s ease-out;
    display: inline-block;
    line-height: 1rem;
    font-weight: 400;
    padding: 1rem 2rem;
    border: .1rem solid #555;
    &:hover {
      color: #FFF;
      background-color: #3498DB;
      border-color: transparent;
    }
  }
}

.preview-article__createdOn {
  line-height: inherit;
}

.preview-article {
  border-bottom: .1rem solid #e8e8e8;
  color: #4f4f4f;
  .preview-article__header {
    a {
      font-size: 2.4rem;
    }
  }
  .content-post__body {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    line-height: 2.35rem;
    // text-align: justify;
    text-indent: 2rem;
    word-break: break-all;
    font-size: 1.4rem;
  }
}

.log-in-container {
  padding-top: 8rem;
  form {
    margin-top: 2rem;
  }
}

.register-container {
  padding-top: 8rem;
  form {
    margin-top: 2rem;
  }
}

.preview-article__title {
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  &:hover {
    color: #3498DB;
  }
}

.article-item {
  margin-bottom: 1rem;
  background-color: #FFF;
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
  border-radius: .3rem;
  border: .1rem solid rgba(0, 0, 0, 0.0901961);
}

@import "article";
