@import "mixins";

.article-banner {
  position: relative;
  @include banner;
  .banner-content {
    // position: absolute;
    bottom: 1rem;
    .title {
      position: absolute;
      bottom: 1rem;
    }
  }
}

.article-detail__createdOn {
  color: #7777bb;
}

.article-detail__title h1 {
  margin-bottom: .5rem;
  border-bottom: .1rem solid rgba(0, 0, 0, 0.3);
}

.article-detail__load-comments {
  visibility: visible;
  opacity: 1;
  height: auto;
  &.u-hide {
    visibility: hidden;
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: visibility 1s, opacity 1s, height 1s 2s linear;
  }
  .button {
    padding: 2rem;
    border: 1px solid rgba(0, 0, 0, .09) !important;
    height: 8rem;
    color: #737373 !important;
    margin-top: 2rem;
    margin-bottom: 0;
    // background-color: #FFF;
    text-align: center;
  }
}

.article-detail__comments-list {
  // background-color:
  padding-bottom: 3rem;
  visibility: hidden;
  height: 0;
  overflow: hidden;
  opacity: 0;
  &.u-hide {
    visibility: visible;
    opacity: 1;
    height: auto;
    transition: visibility 1s, opacity 1s, height 1s 2s linear;
  }
}

.article-detail__comments-item {
  // .article-detail__comment-info {
    // background-color: #FFF;
    padding: 1.5rem 1.5rem 0;
    margin-top: 1rem;
    color: rgba(0, 0, 0, 0.8);
    .article-detail__comment-author {
      margin-bottom: 1rem;
      color: rgba(0, 0, 0, 0.8);
    }
  // }
}
.postMetaInline {
  color: rgba(0, 0, 0, .44) !important;
  fill: rgba(0, 0, 0, .44) !important;
  letter-spacing: 0;
  font-weight: 400;
  font-style: normal;
  .postMetaInline-avatar {
    display: table-cell;


    .avatar {
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      line-height: normal;
      background-color: transparent;
      font-size: 1.4rem;
      // font-weight: 400;
      .avatar-image {
        vertical-align: middle;
        border-radius: 100%;
        display: inline-block;
        border: 0;
        width: 3.6rem;
        height: 3.6rem;
      }
    }

  }

  .postMetaInline-authorLockup {
    display: table-cell;
    // color: rgba(0,0,0,0.8) !important;
    font-size: 1.4rem;
    line-height: 1.4rem;
    padding-left: 1rem;
    text-rendering: auto;
    vertical-align: middle;
    .postMetaInline-createdOn {
      font-size: 1.2rem;
    }
  }

}

.layoutSingleColumn {
  margin: 0 auto;
  // background-color: #FFF;
}
.postMeta {
  margin-bottom: 1rem;
  // background-color: #FFF;
}

p.content {
  font-size: 1.8rem;
  line-height: 1.58;
  letter-spacing: -0.004em;
}

.post-wrapper {
  background-color: #FFF;
  padding: .5rem 1rem;
}

.article {
  background-color: #FFF;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
}
